*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;

}
.search, h1{
    text-align: center;
    color: white;
    margin: 10px 0px;
}
body{
    background-color: rgb(40, 38, 38);
}
.search{
    /* border: 2px solid red; */
    position: relative;
    width: 70%;
    height: 2.2em;
}
input{
    height: 2.3em;
    width: 100%;
    border-radius: 20px;
    padding: 10px;
    font-size: 16px;
    border: none;
}
input:focus{
    border: none;
}
.app{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.search img{
    width: 23px;
    position: absolute;
    right: 10px;
    top: 8px;

}

.movie img{
    height: 311px;
    width: 229px;
}
.container{
    /* border: 2px solid red; */
    display: flex;
    flex-wrap: wrap;
    margin: 22px 0;
    justify-content: center;
    align-items: center;
}
.movie{
    /* border: 2px solid rgb(204, 204, 76); */
    display: flex;
    flex-wrap: wrap;
    width: 239px;
    height: 342px;
}

.detail{
    position: relative;
    bottom: 4.3em;
    font-weight: 500;
    font-size: 17px;
}
.year{
    position: relative;
    top: 2.3em;
    font-weight: bolder;
    font-size: 21px;
}
.app{
    margin: 2px 10%;
}
.img:hover{
    background-color: #1b1a1a;
    opacity: .3;
}